import { sourcesDetail, distributionDetail } from '@/http/api.js';
export default {
	components: {},
	data() {
		return {
			list: [],
			id: '',
			child: [],
			isDis: 0
		};
	},
	onShow() {
		wx.hideHomeButton();
		const pages = getCurrentPages();
		const currPage = pages[pages.length - 1]; // 当前页
		if (currPage.data.id != '') {
			this.id = currPage.data.id;
			this.isDis = currPage.data.isDis;
			this.getDetail(this.id);
		}
	},
	onLoad(options) {
		if (options.isDis && options.isDis == 1) {
			this.isDis = 1;
		}
		this.id = options.id;
		this.getDetail(options.id);
	},
	methods: {
		getDetail(id) {
			if (this.isDis == 1) {
				distributionDetail(id, 'hotel').then(res => {
					this.list = res.data;
				});
			} else {
				sourcesDetail(id, 'hotel').then(res => {
					this.list = res.data;
				});
			}
		},
		tobuy() {
			let _this = this;
			if (!_this.child.length) {
				uni.showToast({
					icon: 'none',
					title: '请选择套餐'
				});
				return;
			}
			if (!uni.getStorageSync('token')) {
				uni.navigateTo({
					url: `/pages/login/login?id=${_this.id}&isDis=${_this.isDis}`
				});
			} else {
				_this.child = _this.child.replace(/\&nbsp;/g, '');
				uni.navigateTo({
					url: `/pages/confirm/hotelConfirm/hotelConfirm?id=${_this.id}&type=hotel&child=${_this.child}&isDis=${_this.isDis}`
				});
			}
			uni.getSetting({
				success(res) {
					// if (!res.authSetting['scope.userInfo']) {
					// if (uni.getStorageSync('token')) {
					// 	uni.navigateTo({
					// 		url: `/pages/login/login?id=${_this.id}&isDis=${_this.isDis}`
					// 	});
					// } else {
					// 	_this.child = _this.child.replace(/\&nbsp;/g, '');
					// 	uni.navigateTo({
					// 		url: `/pages/confirm/hotelConfirm/hotelConfirm?id=${_this.id}&type=hotel&child=${_this.child}&$isDis={_this.isDis}`
					// 	});
					// }
				}
			});
		},
		CheckboxChange(e) {
			this.child = JSON.stringify(e.detail.value);
		}
	},
	// 转发
	// onShareAppMessage() {
	// 	let _this = this;
	// 	return {
	// 		title: '酒店详情',
	// 		path: `/pages/details/hotelDetail/hotelDetail?id=${_this.id}&isDis=${_this.isDis}`
	// 	};
	// }
};
